import React from 'react';
import STATS from "./enums/stats.js";

export default class AggregatedStats extends React.Component {
  render() {
      const stats = this.props.characterStats;
    return (
      <div>
        <p className="subtitle2">Totals</p>
        <div className="AggrigatedStats">
            <ul>
              <li>
                <p className="StatName">Total Health</p>
                <p className="StatValue">{stats.getStat(STATS.HP) * (1 + stats.getStat(STATS.HPPER))}</p>
              </li>
              <li>
                <p className="StatName">Total Attack</p>
                <p className="StatValue">{stats.getStat(STATS.ATK) * (1 + stats.getStat(STATS.ATKPER)) + stats.getStat(STATS.ARTIFACTATK)}</p>
              </li>
              <li>
                <p className="StatName">Total Defense</p>
                <p className="StatValue">{stats.getStat(STATS.DEF) * (1 + stats.getStat(STATS.DEFPER))}</p>
              </li>
            </ul>
        </div>
        <p className="subtitle2">Base</p>
        <div className="AggrigatedStats">
            <ul>
            {Object.keys(STATS).map((property) => {
              const key = STATS[property]
              if (stats.getStat(key) !== 0)
                return  <li key={key}>
                            <p className="StatName">{key}</p>
                            <p className="StatValue">{stats.getStat(key)}</p>
                        </li>
              return '';
            })}
            </ul>
        </div>
      </div>
    );
  }
}