import React from 'react';

export default class ConditionalStat extends React.Component {
    constructor(props) {
      super(props);
      
      this.state = {stacks: this.props.currentStacks};
      this.stacksChanged = this.stacksChanged.bind(this)
      this.stackOptions = this.stackOptions.bind(this)
    }
    stacksChanged(event) {
        let val = parseInt(event.target.value) || 0
            
        this.setState({stacks: val});
        this.props.updateConditionalStacks(this.props.itemId,this.props.itemIndex,val);
    }
    stackOptions(){
        let options = []
        for(let i = 0; i <= this.props.stacks; i++)
            options.push(<option value={i} key={i}>{i}</option>)
        return options;
    }
    render() {
        return (
            <div className="Stat">
                <div className="NumberLabel"><p className="NumberLabelText">{this.props.stat}</p></div>
                <select className="StatPicker" value={this.state.stacks} onChange={this.stacksChanged}>
                    {this.stackOptions()}
                </select>
                <div className="StatPicker">{this.props.value}</div>
            </div>
        );
    }
  }