import React from 'react';
import STATS from "./enums/stats.js";
import Stat from "./stat.js";
import DataEncoder from './util/dataencoder.js';
import Weapons from './data/weapons.js';

export default class WeaponInfo extends React.Component {
    Weapon = {name: 'default'}
    constructor(props) {
      super(props)
      
      
      this.props.forceUpdateWeapon.push(this.updateOnCharacterChange)
      let decoded = this.decodeState(this.props.decodedData)
      console.log("Weapon info init,",this.props.checkWeaponType())
      this.state = {atk: decoded.atk, weaponType:this.props.checkWeaponType(), weapon: decoded.weapon, refinement:decoded.refinement, secondaryValue: decoded.secondaryValue}
      this.atkValueChanged = this.atkValueChanged.bind(this)
      this.secondaryValueChanged = this.secondaryValueChanged.bind(this)
      this.encodeState = this.encodeState.bind(this)
      this.decodeState = this.decodeState.bind(this)
      this.weaponChange = this.weaponChange.bind(this)
      this.getWeaponBonuses = this.getWeaponBonuses.bind(this)
      this.refinementRankChange = this.refinementRankChange.bind(this)
      this.changeWeapon = this.changeWeapon.bind(this)
      this.updateOnCharacterChange = this.updateOnCharacterChange.bind(this)

      this.changeWeapon(Weapons[this.state.weaponType][this.state.weapon])

      this.props.encodeList.push(this.encodeState)
    }
    encodeState(){
      let data = DataEncoder.encodeInt(this.state.atk)
      data.push(parseInt(this.state.weapon),parseInt(this.state.refinement))
      data = data.concat(DataEncoder.encodeFloat(this.state.secondaryValue))
      return data
    }
    decodeState(data){
      console.log(this)
      if (data.length < 6) return {atk: 0, secondaryValue: 0, weapon: 0, refinement: 1};
      let decoded = {atk: DataEncoder.decodeInt(data[0],data[1]),
                     weapon: data[2],
                     refinement: data[3],
                     secondaryValue: DataEncoder.decodeFloat(data[4],data[5])}
      data.splice(0,6)
      this.Weapon = Weapons[this.props.weaponType][parseInt(decoded.weapon)]
      this.props.updateStats(this.props.statItemId,
        this.generateStatMap(decoded.atk,this.Weapon.secondary,decoded.secondaryValue))

      return decoded
    }
    atkValueChanged(val){
      this.setState({atk: val});
      this.props.updateStats(this.props.statItemId,
        this.generateStatMap(val,this.Weapon.secondary,this.state.secondaryValue))
    }
    secondaryValueChanged(value){
      this.setState({secondaryValue: value})
      this.props.updateStats(this.props.statItemId,
        this.generateStatMap(this.state.atk,this.Weapon.secondary,value))
    }
    generateStatMap(atk,secondaryStat,secondaryVal){
      let m = new Map()
      m[STATS.ATK] = parseFloat(atk) || 0;
      if (secondaryStat === STATS.ATK)
        m[STATS.ATK] += parseFloat(secondaryVal) || 0;
      else
        m[secondaryStat] = parseFloat(secondaryVal) || 0;
      return m;
    }
    mapWeapons(weaponType){
      var i = 0
      if(typeof weaponType === 'undefined'){
        console.log('undefined weapon type, defaulting to bow')
        weaponType = 'bow';
      }
      return Weapons[weaponType].map((weapon) => {
        return <option key={i} value={i++}>{weapon.name}</option>
    })}
    weaponChange(event){
      let val = parseInt(event.target.value) || 0;
      let newWeapon = Weapons[this.state.weaponType][parseInt(val)]
      if(typeof newWeapon === 'undefined'){
        newWeapon = Weapons[this.state.weaponType][0]
        this.setState({weapon:0})
      }
      else
          this.setState({weapon:val})
      this.changeWeapon(newWeapon)
      this.props.updateStats(this.props.statItemId,
        this.generateStatMap(this.state.atk,this.Weapon.secondary,this.state.secondaryValue))
    }
    changeWeapon(weapon){
      if (weapon.name === this.Weapon.name)
        return
      if (this.Weapon.name !== 'default' && this.Weapon.hasOwnProperty('bonus'))
        this.props.removeConditionals(this.Weapon.name)
      if(weapon.hasOwnProperty('bonus'))
      {
        let bonus = weapon.bonus.map((stat)=>{
          return {stat:stat.stat, value:stat.base + stat.incriment * this.state.refinement, stacks:stat.stacks || 1}
        })
        this.props.addConditionals(weapon.name,bonus)
      }
      this.Weapon = weapon
    }
    refinementRankChange(event){
      let val = parseInt(event.target.value) || 0;
      if (val < 1 || val > 5)
        val = parseInt(event.target.value.slice(-1)) || 1
      if (val < 1)
        val = 1
      else if (val > 5)
        val = 5
      this.changeWeapon(this.Weapon)
      this.setState({refinement:val})
    }
    getWeaponBonuses(weapon){
      let bonuses = []
      if (weapon.hasOwnProperty('bonus')) {
          let bonus = weapon['bonus']
          bonuses.push(
              bonus.map((stat) => {
                  return <div key={stat.stat}>{stat.stat} : {stat.base + stat.incriment * this.state.refinement}</div>
              })
          )
      }
      return bonuses;
  }
  updateOnCharacterChange(character,weaponType)
  {
    console.log("changeweapon")
    this.changeWeapon(Weapons[weaponType][this.state.weapon])
    this.setState({weaponType:weaponType})
  }
      render() {
        return (
          <div className="WeaponInfo">
                <select className="CharacterPicker" name="CharacterPicker" value={this.state.weapon} onChange={this.weaponChange}>
                    {this.mapWeapons(this.state.weaponType)}
                </select>
              <Stat stat={STATS.ATK} value={this.state.atk} valueChanged={this.atkValueChanged}/>
              <Stat stat={this.Weapon.secondary || STATS.NONE} value={this.state.secondaryValue} valueChanged={this.secondaryValueChanged}/><div className="Stat">
              <div className="NumberLabel">Refinement Rank</div> 
                <input type="text" min="1" max="5" step="1"  className="EntryBox"
                        value={this.state.refinement} onChange={this.refinementRankChange}/>
              </div>
              <p>Bonus Stats</p>
              {this.getWeaponBonuses(this.Weapon)}
          </div>
        );
      }
  }