import React from 'react'
import ConditionalStat from './conditionalstat.js'

export default class ConditionalStats extends React.Component {

    mapStats(items){
        let keys =[ ...items.keys() ];
        return keys.map((key) => {
            let i = -1
            let item = items.get(key)
            if (item.length === 0) return ''
            return <div key={key}><p className="subtitle3">{key}</p>
            {item.map((stat) => {
                ++i
                return <div key={i}><ConditionalStat itemId={key} itemIndex={i} stat={stat.stat} value={stat.value} currentStacks={stat.currentStacks || 0} stacks={stat.stacks || 1} conditionalValues={this.conditionalValues} updateConditionalStacks={this.props.updateConditionalStacks}/></div>
            })}
            </div>

        })
    }
      render() {
        return (
            <div className="BoxInfo">
                <p className="subtitle2">Conditional</p>
                <div className="ConditionalLayout">
                <div className="ConditionalLabel">
                    <div className="NumberLabel"><p className="formatLabel">name</p></div>
                    <div className="StatPicker">
                    <p className="formatLabel">stacks</p>
                    </div>
                    <div className="StatPicker">
                    <p className="formatLabel">value</p>
                    </div>
                </div>
                {this.mapStats(this.props.stats)}
                </div>
            </div>
        );
      }
  }