import React from 'react';
import CharacterStats from "./characterstats.js";
import CharacterInfo from "./characterinfo.js";
import WeaponInfo from "./weaponinfo.js";
import Artifacts from "./artifacts.js";
import AggregatedStats from "./aggregatedstats.js"
import TalentInfo from "./talentinfo.js"
import DataEncoder from './util/dataencoder.js'
import ConditionalStats from './conditionalstats.js'
const URL = "https://daynar100.github.io/GenshinImpactCharacterBuilder/?"

export default class Loadout extends React.Component {
    weaponType
    constructor(props) {
      super(props);
      let str = window.location.search
      str = str.substring(str.indexOf('?') + 1,str.length)
      this.state = {character: 'Jean',weaponType:'bow', forceUpdateOnCharacterChangeList: [], characterStats: new CharacterStats(), conditionalStats: new Map(), buildUrl:'', encodeList:[], decodedData: (new DataEncoder()).decode(str)};
      this.talentLevelChange = this.talentLevelChange.bind(this)
      this.generateUrl = this.generateUrl.bind(this)
      this.changeCharacter = this.changeCharacter.bind(this)
      this.addConditionals = this.addConditionals.bind(this)
      this.removeConditionals = this.removeConditionals.bind(this)
      this.updateConditionalStacks = this.updateConditionalStacks.bind(this)
      this.checkWeaponType = this.checkWeaponType.bind(this);
    }
    generateUrl(){
      let data = [];
      let size = this.state.encodeList.length;
      for(let i = 0; i < size; ++i){
        data = data.concat(this.state.encodeList[i]())
      }
      this.setState({buildUrl: URL + ((new DataEncoder()).encode(data))})
    }
    updateCharacterStats = (itemId,stats) => {
      let cs = this.state.characterStats;
      cs.updateItem(itemId,stats);
      this.setState({characterStats: cs});
    }
    talentLevelChange(number,value){
      let cs = this.state.characterStats;
      cs.talentLevel[number] = value;
      this.setState({characterStats: cs});
    }
    changeCharacter(name,weaponType){
      console.log("change character",name,weaponType)
      this.weaponType = weaponType
      for(let i = 0; i < this.state.forceUpdateOnCharacterChangeList.length; ++i)
        this.state.forceUpdateOnCharacterChangeList[i](name,weaponType)
      this.setState({character:name,weaponType:weaponType})
    }
    checkWeaponType(){
      return this.weaponType;
    }
    addConditionals(effectName,conditionals){
      var cs = this.state.conditionalStats
      if (cs.has(effectName)){
        //console.log('Adding to existing conditional. This should not happen.')
        cs.set(effectName,conditionals)
      }
      else {
        cs.set(effectName,conditionals)
      }
      
      let charStats = this.state.characterStats;
      charStats.updateItem("conditional",this.getConditionalStatMap(cs))
      this.setState({conditionalStats: cs,characterStats:charStats})
    }
    removeConditionals(effectName){
      var cs = this.state.conditionalStats
      if (cs.has(effectName)){
        cs.delete(effectName)
      }
      else {
        console.log('Attempting to remove nonexisting conditionals for ' + effectName)
      }
      let charStats = this.state.characterStats;
      charStats.updateItem("conditional",this.getConditionalStatMap(cs))
      this.setState({conditionalStats: cs,characterStats:charStats})
    }
    updateConditionalStacks(key, index, stacks){
      let cs = this.state.conditionalStats
      cs.get(key)[index].currentStacks = stacks
      let charStats = this.state.characterStats;
      charStats.updateItem("conditional",this.getConditionalStatMap(cs))
      this.setState({conditionalStats: cs,characterStats:charStats})
    }
    getConditionalStatMap(conditionalStats){
      let statMap = new Map()
      
      let keys =[ ...conditionalStats.keys() ];
      for(let key in keys){
        let item = conditionalStats.get(keys[key])
        for(let index in item) {
          let stat = item[index]
          if (stat.stat in statMap)
            statMap[stat.stat] += stat.value * stat.currentStacks || 0
          else
            statMap[stat.stat] = stat.value * stat.currentStacks || 0
        }
      }
      return statMap;
    }
    render() {
      return (
        <div className="Loadout">
            <BuildInfo />
          <div className="Loadout-body">
            <div className="PrimarySection">
              <div className="BoxInfo">
                <div className="UrlGenerator">
                  <button type="button" onClick={this.generateUrl}>Generate URL</button>
                  {document.queryCommandSupported('copy') && 
                  <button type="button" onClick={(event) => {
                    const el = document.getElementById('GeneratedUrl')
                    el.select()
                    document.execCommand('copy')
                  }}>Copy</button>}
                  <textarea id='GeneratedUrl' className="GeneratedUrl" cols='63' value={this.state.buildUrl} readOnly={true}/>
                </div>
              </div>
            </div>
            <div className="subtitle">Character Info</div>
            <div className="PrimarySection">
              <div className="BoxInfo">
                <CharacterInfo statItemId="character" changeCharacter={this.changeCharacter} decodedData={this.state.decodedData} updateStats={this.updateCharacterStats} talentLevelChange={this.talentLevelChange} encodeList={this.state.encodeList} addConditionals={this.addConditionals} removeConditionals={this.removeConditionals}/>
              </div>
              <div className="BoxInfo">
                <WeaponInfo statItemId="weapon" weaponType={this.state.weaponType} decodedData={this.state.decodedData} updateStats={this.updateCharacterStats} encodeList={this.state.encodeList} addConditionals={this.addConditionals} removeConditionals={this.removeConditionals} forceUpdateWeapon={this.state.forceUpdateOnCharacterChangeList} checkWeaponType={this.checkWeaponType}/>
              </div>
            </div>
            <div className="subtitle">Artifacts</div>
            <Artifacts decodedData={this.state.decodedData} updateStats={this.updateCharacterStats} encodeList={this.state.encodeList} addConditionals={this.addConditionals} removeConditionals={this.removeConditionals}/>
            <div className="subtitle2">
              <p>The following sections are works in progress with info gathered from wikis and other community driven sources. They will not be 100% correct.
              If you notice an issue please report it at https://github.com/Daynar100/GenshinImpactCharacterBuilder/issues
              </p></div>
            <div className="subtitle">Stats</div>
            <div className="PrimarySection">
              <ConditionalStats statItemId="conditional" stats={this.state.conditionalStats} updateConditionalStacks={this.updateConditionalStacks}/>
              <div className="BoxInfo">
            <AggregatedStats characterStats={this.state.characterStats}/>
            </div>
            </div>
            <TalentInfo character={this.state.character} characterStats={this.state.characterStats}/>
          </div>
        <div>
          <p>
            This site is not affiliated with or endorsed by miHoYo.
          </p>
        </div>
        </div>
      );
    }
}

class BuildInfo extends React.Component {
    render() {
      return (
        <div className="BuildInfo">
            {/*Build Name: 
          <input type="text"/>*/}
          <p className="title">Genshin Impact Character Builder</p>
        </div>
      );
    }
}